'use strict';
angular
	.module('ngWig')
	.config([
		'ngWigToolbarProvider',
		function(a) {
			a.addCustomButton('forecolor', 'nw-forecolor-button');
		}
	])
	.component('nwForecolorButton', {
		bindings: { execCommand: '=', editMode: '=', disabled: '=' },
		template:
			'<button colorpicker type="button" ng-model="fontcolor" ng-disabled="$ctrl.editMode || $ctrl.disabled" colorpicker-position="right" class="nw-button font-color" title="Font Color">Font Color</button>',
		controller: [
			'$scope',
			function(a) {
				var b = this;
				a.$on('colorpicker-selected', function(a, c) {
					b.execCommand('foreColor', c.value);
				});
			}
		]
	});
